.title {
	font-size: 30px;
	margin-bottom: 8px;
}

.text {
	font-family: 'Approach';
	font-weight: 400;
	font-size: 18px;
}


* {
  box-sizing: border-box;
  scrollbar-width: none;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  display: none;
  -webkit-appearance: none;
}

html, body {
	height: 100%;
	background-color: #000000;
	overflow: hidden;
}

img {
	user-drag: none;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-drag: none;
	-webkit-user-select: none;
	-ms-user-select: none;
}

.page {
	background-image: radial-gradient(#2b2b2b, #000000);
	overflow: hidden;
}

.page__inner {
	padding: 32px;
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
}

@media (max-width: 600px) {
	.page {
		background-image: radial-gradient(#3c3c3c, #000);
	}
}



.contact-card {
	width:100%;
	max-width: 230px;
	height: 351px;
	
	&__inner {
		-webkit-transform: rotate(15deg) perspective(2000px) translateX(16px);
						transform: rotate(15deg) perspective(2000px) translateX(16px);
		height: 100%;
		position: relative;
		-webkit-transform-style: preserve-3d;
						transform-style: preserve-3d;
	}

	
	&__side {
		position: absolute;
		top:0;
		left:0;
		height: 351px;	
		width:100%;
		background: $black;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
		padding: 24px;
		margin-bottom: 0;
		-webkit-transform-origin: 50% 50% 0;
				-ms-transform-origin: 50% 50% 0;
						transform-origin: 50% 50% 0;
		-webkit-transform-style: preserve-3d;
						transform-style: preserve-3d;
		
		
		&--front { -webkit-transform: translateZ(0px); transform: translateZ(0px); }
		&--back { -webkit-transform: translateZ(-5px) rotateY(180deg); transform: translateZ(-5px) rotateY(180deg); }
	
	}
	
	&__logo {
			margin-top: 16px;
			width: 100%;
			margin-bottom: 8px;
		}

	&__title {
color: $white;
font-size: 16.3px;
text-align: center;
}

&__address-item {
color: $white;
text-decoration: none;
font-size: 11.5px;
line-height: 1.4;
display: block;
font-weight: 700;
font-family: "Approach Mono";
}


&__thick {
content: '';
position: absolute;
top: 0;
bottom: 0;
height: 351px;
z-index: -10;
-webkit-transform-origin: 0% 0%;
		-ms-transform-origin: 0% 0%;
				transform-origin: 0% 0%;
-webkit-transform: rotateY(90deg) translateY(-0%) translateX(0%);
				transform: rotateY(90deg) translateY(-0%) translateX(0%);
background-color: white;

&--left {
	height: 351px;
	width: 2.5px;
	top: 0;
	bottom: 0;
	left: 0;
}

&--right {
	height: 351px;
	width: 2.5px;
	top: 0;
	bottom: 0;
	right: -2.5px;
}

&--top {
	-webkit-transform: rotateX(-90deg);
					transform: rotateX(-90deg);
	right: 0;
	height: 2.5px;
	width: 100%;
	right: 0;
}

&--bottom {
	-webkit-transform: rotateX(-90deg);
					transform: rotateX(-90deg);
	right: 0;
	height: 2.5px;
	width: 100%;
	right: 0;
	top: unset;
	bottom: -2.5px;
}

}
}

@media (max-width: 400px) {
	
	.contact-card {
		max-width: 200px;
		width: 200px;
		height: 160px;

		&__address-item {
			font-size: 9.5px;
		}

		&__title {
			font-size: 13.4px;
		}

	}

}





















.qrcode {
	position: absolute;
	bottom: -100px;
	
	&__image {
		border: 5px solid $white;
		display: block;
		width: 50px;
		height: 50px;
	}


}